          videoIMg:'',
          isChoose:'',
          isChat:false,
          Chat:'',
          jionDress:[],
          set_address:'',
          languageID:'',
          best_introduce:"",
          best_descripte:"",
          isvolunteer:'',
          needLanguage:'',
