          isEmail:false,
          reacetWho:'',
          emailCenter:'',
          return_DL:'',
          max_height:'',
          discount:[],
          isDiscount:true,
          Differ:[],
          is_combine_qinzi:'',
          is_combine_zuhe:'',
          arr:[
            {img:"../../../static/img/publish/icon_1.png"},
            {img:"../../../static/img/publish/icon_2.png"},
            {img:"../../../static/img/publish/icon_3.png"},
            {img:"../../../static/img/publish/icon_4.png"},
            {img:"../../../static/img/publish/icon_5.png"},
          ],
          allText:{
            content:[
              {
                'zh':'',
