        }
      },
      components: {
        Head,
        Person,
        Map,
        Detail,
        Foot,
        Bounced,
        Loading,
        loadingImg,
        Chat
      },
      mounted(){
        let _this = this
        _this.nextOver = true
        if(localStorage.getItem('isLogin')){
          _this.getOwers()
        }
        _this.ScrollContent()
        _this.sendBuidu()
        _this.getComment(1)
        _this.getActls()
        _this.imgUrl = localStorage.getItem('userImg')
      },
      created:function(){
        this.language = test()
        this.policy = policy()
        this.getParams()
        window.scroll( 0 ,0)
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        if(flag){
          this.$router.push({
            path: '/mPublishPage',
            query:{
              information: this.$route.query.information
            }
          })
        }
      },
      watch:{
        '$route':'getParams',
        wishNamea:function(){
          Bus.$emit('returnName',this.wishNamea)
        },
        routerParams:function(){
          window.scroll(0,0)
          this.imgList = []
          this.personList = []
          this.activeList = []
          this.dayList = []
        },
        province:function(){
          Bus.$emit('jionDress',this.jionDress)
          Bus.$emit('tyas',1)
        },
        imgList:function(){
          var a = []
          var b = []
          for(let i =0;i<this.imgList.length;i++){
            if(this.imgList[i].extension != 'mp4'){
              a.push({img:this.imgList[i].domain + this.imgList[i].image_url})
            }else{
              b.push({img:this.imgList[i].domain + this.imgList[i].image_url})
            }
          }
          this.photoList = a
          this.videoList = b
        }
      },
      computed:{
        Lan(){
          return this.Store.getters.getValue
        }
      },
      methods:{
        sendBuidu(){
          if(window.location.href.indexOf('.top') == -1 && window.location.href.indexOf('localhost') == -1){
            this.$http.post(this.api + '/BaiduPush',{
              token: localStorage.getItem('token'),
              url: encodeURIComponent(window.location.href)
            })
              .then(res=>{
                if(res.data.code == 1){
                }else if(res.data.code == 3){
                  this.sendBuidu()
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        },           
        //
        chat(){
          this.isChat = false
        },
        lookclick(item,index){
          this.photoIndex = index
        },
        closeLook(){
          this.looKing = false
          this.photoIndex = 0
          this.left = 0
        },
        prev(){
          if(this.photoIndex > 3){
            this.photoIndex --
            this.left += 114
          }else if(this.photoIndex >0&& this.left <0){
            this.photoIndex --
            this.left += 114
          }else if(this.photoIndex >0&& this.left >=0){
            this.photoIndex --
          }
        },
        next(){
          if(this.photoIndex < this.lookImage.length -1){
            this.photoIndex ++
            if(this.photoIndex > 3){
              this.left -= 114
            }
          }
        },
        changeIndex(val){
          if(val==1){
            this.changeColor = 1
            this.lookList = this.photoList
          }else{
            this.changeColor = 2
            this.lookList =  this.videoList
          }
        },
        closeLookImg(){
          this.isLooking = false
        },
        clickImage(val){
          this.videoIMg = val
          this.viedio = true
        },
        //
        ScrollContent(){
          let a = window.screen.availHeight
    
          this.max_height = a*0.6
        
        },
        looKingP(){
          this.isLooking = true
          if( this.changeColor = 1){
            this.lookList = this.photoList
          }else{
            this.lookList = this.videoList
          }
        },
        //
        lookHouseImg(item,index){
          this.looKing = !this.looKing
          if(this.isflag){
            this.flag = item.flag
          }
           this.lookImage = this.houseImage
        },
        //
        lookIng(item,index){
          this.lookImg = item.domain + item.image_url
          this.lookIndex = true
        },
        closeWeixin(){
          this.isWeixin = false
        },
        //
        toWeixin(){
          var url =window.location.href + '&'+ 'userId' +'='+this.isOwer
          this.$http.post(this.api + '/home/Upload/getqrcode',{
            token: localStorage.getItem('token'),
            url:url
          })
            .then(res=>{
              if(res.data.code == 1){
                this.isShare = false
                this.isWeixin = true
                this.weixin = res.data.data.src
               
              }
            })
        },
        //
        toWeiBo(){
          var p = {
            // ( )
            url: window.location.href + '&'+'userId' +'='+this.isOwer,
            // title( )
            title: ''+ this.title,
            // ( )
            pic: '',
            //(zh_cn | zh_tw)()
            language: 'zh_cn'
          };
          var s = [];
          for(var i in p) {
            s.push(i + '=' + encodeURIComponent(p[i] || ''));
          }
          var url = "http://service.weibo.com/share/share.php?" + s.join('&');
          window.open(url);
        },
        //
        toTranslation(){
          this.$router.push({
            path: '/translation',
            query:{
              activeId: this.routerParams
            }
          })
        },
        toEmail(){
          this.isEmail = true
          this.isShare = false
        },
        shreEmail(){
          var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
          if(!reg.test(this.reacetWho)){
            this.$message({type:'info',message:''})
          }else{
            this.$http.post(this.api + '/EmailShare',{
              token: localStorage.getItem('token'),
              toemail: this.reacetWho,
              title: this.emailCenter,
              content: this.shareContent,
              url:window.location.href + '&'+'userId' +'='+this.isOwer,
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.$message({type:'success',message:''})
                  this.isEmail = false
                }else if(res.data.code == 3){
                  this.toEmail()
                }else{
                  this.$message({type:'error',message:res.data.msg})
                }
              })
          }

        },
        //
        saveTranslate(){
          this.$http.post(this.api + '/TranslateS',{
            token: localStorage.getItem('token'),
            activity_id: this.routerParams,
            t_introduce: this.first.textarea,
            t_descripte: this.first.content,
            user_id: this.isOwer,
            language: this.first.language
          })
            .then(res=>{
              if(res.data.code == 1){
                this.tiYan = false
                this.first = {
                  textarea:'',
                  content:'',
                  language:''
                }
                this.$message({
                  type:'success',
                  message: ''
                })
              }else if(res.data.code == 3 || res.data.code == 4){
                this.saveTranslate()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        //
        orderVolunter(){
          if(localStorage.getItem('isLogin') && this.isvolunteer){
            this.$router.push({
              path: '/order',
              query:{
                information: this.routerParams,
                orderVolunteer: 1
              }
            })
          }else{
            alert('')
          }
        },
        getName(data){
          this.wishNamea = data
        },
        addWishName(){
          if(this.wishNamea == ' '||this.wishNamea.split(' ').join("").length == 0){
            this.$message({
              type:'error',
              message: ''
            })
          }else{
            this.$http.post(this.api + '/home/Comment/add_collegroup',{
              token: localStorage.getItem('token'),
              group_name: this.wishNamea
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.wishNamea = ''
                  this.getRevie();
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.addWishName()
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        },

        prevIndex(){
          if(this.pageIndex > 0 ){
            this.pageIndex --
            this.nextOver = true
            let list = document.getElementById('b')
            list.style.left = parseInt(list.style.left) + 300 +'px'
            if(this.pageIndex == 0){
              this.prevOver = false
            }
          }
          },
          nextIndex(){
          if(this.pageIndex < this.listA.length - 3){
            this.prevOver = true
            this.pageIndex ++
            let list = document.getElementById('b')
            list.style.left = parseInt(list.style.left) - 300 + 'px'
            if(this.pageIndex == this.listA.length - 3){
              this.nextOver = false
            }
          }
          },
        toDate(){
          if(localStorage.getItem('isLogin')){
            this.$router.push({
              path: '/order',
              query:{
                information: this.routerParams,
                isflag: this.isflag
              }
            })
          }else{
            alert('')
          }
        },
        toChoose(item,index){
          if(localStorage.getItem('isLogin')){
            if(this.isChoose){
              if(this.long_day == '2'){
                this.$confirm(', ?', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                  var a = []
                  if(item.list){
                    for(let i = 0;i<item.list.length;i++){
                      if(i == index){
                        a=[{day:item.day,list:item.list[i],status:item.status}]
                      }
                    }
                  }else{
                    a.push(item)
                  }
                  this.$router.push({
                    name: 'allHouse',
                    query:{
                      activeId: this.routerParams,
                      slot_id:!item.list?item.slot_id:item.list[index].slot_id,
                      chooseTime:JSON.stringify(a)
                    }
                  })
                }).catch(() => {
                  var a = []
                  if(item.list){
                    for(let i = 0;i<item.list.length;i++){
                      if(i == index){
                        a=[{day:item.day,list:item.list[i],status:item.status}]
                      }
                    }
                  }else{
                    a.push(item)
                  }
                  this.$router.push({
                    name: 'choose',
                    query:{
                      activeId: this.routerParams,
                      chooseTime: JSON.stringify(a)
                    }
                  })
                });
              }else{
                var a = []
                if(item.list){
                  for(let i = 0;i<item.list.length;i++){
                    if(i == index){
                      a=[{day:item.day,list:item.list[i],status:item.status}]
                    }
                  }
                }else{
                  a.push(item)
                }
                this.$router.push({
                  name: 'choose',
                  query:{
                    activeId: this.routerParams,
                    chooseTime: JSON.stringify(a)
                  }
                })
              }
            }else{
              this.$message({
                type: 'info',
                message: ''
              })
              localStorage.setItem('editRouter','editcheck')
              localStorage.setItem('editIndex',2)
              this.$router.push({
                path: '/editcheck'
              })
            }
          }else{
            alert('')
          }
        },
        contactPlanner(){
          if(localStorage.getItem('isLogin')){
            this.isChat = true
          }else{
            alert('!')
          }

        },
        handleCurrentChange(){
          this.pager += 1 
          this.getComment(this.pager)
          
        },

        textareaFocus(){
          this.height = 'auto'
          this.overflow = 'none'
        },
        abolish(){
          this.review.text = ''
          this.height = '78px'
          this.overflow= 'hidden'
          this.replay = '...'
          this.NowChat = false
        },
        toAllLeave(a){
          localStorage.setItem('leave',JSON.stringify(a))
          this.$http.post(this.api + '/LeaveL',{
            token: localStorage.getItem('token'),
            flag: 4,
            order:1,
            page: 1,
            table_id: a.comment_id
          })
            .then(res=>{
              if(res.data.code == 1){
                a.leavemsg = res.data.data.data
                this.replayChoose = true
              }else if(res.data.code == 3 || res.data.code == 4){
                this.toAllLeave(a)
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        discuss(){
          if(!this.review.text){
            this.$message({
              type: 'info',
              message: ''
            })
          }else{
            if(this.replay == '...'){
              this.$http.post(this.api + '/CommentS',{
                token: localStorage.getItem('token'),
                flag: 1,
                table_id: this.routerParams,
                content:  this.review.text
              })
                .then(res=>{
                  if(res.data.code == 1){
                    this.review.text = ''
                    this.$message({
                      message:'',
                      type:'success'
                    })
                    this.getComment(1)
                  }else if(res.data.code == 3 || res.data.code == 4){
                    this.discuss()
                  }else if(res.data.code == 0){
                    alert(res.data.msg)
                  }
                })
            }else{
              let flag = ''
              if(this.saveLeave){
                flag = 5
              }else {
                flag = 4
              }
                this.$http.post(this.api + '/home/Comment/save_leavemsg',{
                  token: localStorage.getItem('token'),
                  flag: flag,
                  content: this.review.text,
                  table_id: localStorage.getItem('comment_id')
                })
                  .then(res=>{
                    if(res.data.code == 1){
                      localStorage.removeItem('comment_id')
                      this.review.text = ''
                      this.$message({
                        message:'',
                        type:'success'
                      })
                      this.NowChat = false
                      this.replay = '...'
                      if(this.replayChoose){
                        let a = JSON.parse(localStorage.getItem('leave'))
                        this.toAllLeave(a)
                      }else{
                        this.getComment(this.pager)
                      }
                      this.saveLeave = false
                    }else if(res.data.code == 3 || res.data.code == 4){
                      this.discuss()
                    }else if(res.data.code == 0){
                      alert(res.data.msg)
                    }
                  })
              }
          }
        },
        getParams:function(){
          this.routerParams = this.$route.query.information
          if(this.$route.query.translate_id){
            this.translate_id = this.$route.query.translate_id
          }
          var userLanguage = navigator.language.substr(0,2)
          for(let i = 0; i< this.language.length;i++){
            if(this.language[i].name == userLanguage){
              this.languageID = i
            }
          }
          this.getActive(this.translate_id )
          this.getDiscount()
          this.getDiffer()
          this.getHouseImg()
          this.getSlot()
        },
        getSlot(){
          this.$http.post(this.api + '/ActivitySlotUserTwo',{
            token: localStorage.getItem('token'),
            verson: 2.0,
            activity_id: this.routerParams
          })
          .then(res=>{
            if(res.data.code == 1){
              let data  = res.data.data
              if(data.length){
                for(var i = 0; i< data.length;i++){
                  if(data[i].status == 0&& data[i].online != 1){
                    this.orderNum = data[i].max_person_num
                  }
                }
                
              }
              
            }else if(res.data.code == 3){
              this.getSlot()
            }else if(res.data.code == 0){
              this.$message({type: 'error',message:res.data.msg})
            }
          })
        },
        getHouseImg(){
          this.$http.post(this.api + '/ActivityHouseImageTwo',{
            token: localStorage.getItem('token'),
            verson: 2.0,
            activity_id: this.routerParams,
          })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              this.houseImage = data
            }else if(res.data.code == 3){
              this.getHouseImg()
            }else if(res.data.code == 0){
              this.$message({type: 'error',message:res.data.msg})
            }
          })
        },
        getDiffer(){
          this.$http.post(this.api + '/DifferListTwo',{
            token: localStorage.getItem('token'),
            verson: 2.0,
            activity_id: this.routerParams
          })
          .then(res=>{
            if(res.data.code == 1){
             
              this.Differ = res.data.data
            }else if(res.data.code == 3){
              this.getDiffer()
            }else if(res.data.code == 0){
              this.$message({type: 'error',message:res.data.msg})
            }
          })
        },
        getDiscount(){
          this.$http.post(this.api + '/ActivityDiscountTwo',{
            token: localStorage.getItem('token'),
            verson: 2.0,
            activity_id: this.routerParams
          })
          .then(res=>{
            if(res.data.code == 1){
              this.discount = res.data.data
            }else if(res.data.code == 3){
              this.getDiscount()
            }else if(res.data.code == 0){
              this.$message({type: 'error',message:res.data.msg})
            }
          })
        },
        getActive(val){
          this.isLoading = true
          this.$http.post(this.api + '/ActivityDetailTwo',{
            token: localStorage.getItem('token'),
            activity_id: this.routerParams,
            translate_id:val,
            language:this.languageID,
            visit: 1,
            verson:2.0
          })
            .then(res=>{
              if(res.data.code == 1){
                let data = res.data.data
                this.imgList.push(data.cover)
            
                for(let i =0;i<data.image.length;i++){
                    this.imgList.push(data.image[i])
                }
                this.title = data.title
                document.title = data.title
                if(data.user.head_image){
                  this.userImg = data.user.headimage?data.user.headimage.domain + data.user.headimage.themb_url:'../../../static/img/static/user.png'
                }else{
                  this.userImg = '../../../static/img/static/user.png'
                }
                this.userintroduce = data.introduce
                this.content = data.descripte
                this.attation = data.activ_notice
                this.goPlace = data.go_place
                this.is_volunteen = data.is_volunteen
                this.activ_bring = data.activ_bring
                this.province = data.province+' ' +data.city+' '+ data.region
                this.jionDress = [data.set_address_lng,data.set_address_lat]
                this.total_time = data.total_time
                this.userName =(data.user.family_name?data.user.family_name+' ':'' )+(data.user.middle_name?data.user.middle_name+' ':'')+data.user.name
                this.activ_provite = data.activ_provite
                this.main_language = this.language[data.main_laguage].label
                this.score = parseInt(data.score)
                this.return_policy = this.policy[data.return_policy].label
                this.return_DL = data.return_policy
                this.return_content = data.return_content
                this.age_limit = data.age_limit
                this.userId = data.user.user_id
                this.set_address = data.set_address
                this.best_descripte = data.best_descripte
                this.best_introduce = data.best_introduce
                this.price = data.price
                this.is_combine_qinzi = data.is_combine_qinzi
                this.is_combine_zuhe = data.is_combine_zuhe
                if(data.volun_laguage){
                  var a = []
                  for(let m = 0;m<data.volun_laguage.split(',').length;m++){
                    a.push(parseInt(data.volun_laguage.split(',')[m]))
                  }
                  this.needLanguage = a
                }
                if(data.other_laguage){
                  var a = []
                  for(let m = 0;m<data.other_laguage.split(',').length;m++){
                    a.push(parseInt(data.other_laguage.split(',')[m]))
                  }
                  this.other_laguage = a
                }
                this.volun_require =  data.volun_require
                if(data.issatay == 2){
                  this.long_day = '1'
                  // this.houseImage = data.houseimage
                  this.isflag = false
                }else if(data.issatay == 1){
                  this.isflag = true
                  this.long_day = '2'

                }else{
                  this.long_day = ''
                }
                this.is_collection = data.is_collection
                if(localStorage.getItem('isLogin')){
                  if(data.is_order || this.userId == this.isOwer){
                    this.isLogin = true
                  }else{
                    this.isLogin = false
                  }
                }else{
                  this.isLogin = false
                }
                this.isLoading = false
               
              }else if(res.data.code == 3 || res.data.code == 4){
                this.getParams()
              }else if(res.data.code == 0 ){
                alert(res.data.msg)
              }
            })
        },
        getRevie(){
          this.$http.post(this.api + '/home/Comment/collegroup_list',{
              token: localStorage.getItem('token'),
              table_id: this.routerParams,
              flag: 1
            })
              .then(res=>{
                if(res.data.code == 1){
                this.wishList = res.data.data
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.getRevie()
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              }) 
        },
        like(){
          if(localStorage.getItem('isLogin')){
            this.isLike = !this.isLike
            this.getRevie()
          }else{
            this.$message({
              type: 'error',
              message: ''
            })
          }
        },
        closeWish(){
          this.isLike = false
        },
        share(){
          this.isShare = !this.isShare
        },
        closeShare(){
          this.isShare = false
        },
        openInform(item,b){
          if(b == 1){
            localStorage.setItem('comment_id',item.comment_id)
          }else{
            this.saveInfor = true
            localStorage.setItem('comment_id',item.msg_id)
          }
          this.isInform = true
          this.getInform()
        },
        changeForm(){
          this.changeReason = false
        },
        informSend(){
          if(this.informReason.text.trim().length <= 0){
            this.$message({
              type:'error',
              message: ''
            })
          }else{
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: 2,
              table_id: localStorage.getItem('comment_id'),
              content: this.informReason.text
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  this.informReason.text = ''
                  this.changeReason = true
                  this.$message({
                    type:'success',
                    message:''
                  })
                  localStorage.removeItem('comment_id')
                  this.getComment(this.pager)
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.informSend()
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        },
        informAblish(){
          this.changeReason = true
        },
        closeInform(item){
          if(item){
            let flag = ''
            if(this.saveInfor){
              flag = 4
            }else{
              flag = 2
            }
            this.$http.post(this.api + '/ReportU',{
              token: localStorage.getItem('token'),
              flag: flag,
              table_id: localStorage.getItem('comment_id'),
              option_id: item.option_id
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.isInform = false
                  localStorage.removeItem('comment_id')
                  this.getComment(this.pager)
                  this.$message({
                    type:'success',
                    message: ''
                  })
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.closeInform(item)
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }else{
            this.isInform = false
            localStorage.removeItem('comment_id')
          }

        },
        leaveTalk(a,b){
          if(a.user_id == this.isOwer){
            this.$http.post(this.api + '/home/Comment/del_leavemsg',{
              token: localStorage.getItem('token'),
              msg_id: a.msg_id
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.$message({
                    type: 'success',
                    message:''
                  })
                  this.getComment(this.pager)
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.leaveTalk(a,b)
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }else{
            this.NowChat = true
            this.replay = '' + a.user.name
            localStorage.setItem('comment_id',a.msg_id)
            this.saveLeave = true
          }
        },
        talk(a,b){
          if(a.user_id == this.isOwer){
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              this.$http.post(this.api + '/home/Comment/del_comment',{
                token: localStorage.getItem('token'),
                comment_id: a.comment_id
              })
                .then(res=>{
                  if(res.data.code == 1){
                    this.personList.splice(b,1)
                    this.$message({
                      type: 'success',
                      message:''
                    })
                  }else if(res.data.code == 3 || res.data.code == 4){
                    this.talk(a,b)
                  }else if(res.data.code == 0){
                    alert(res.data.msg)
                  }
                })
            }).catch(() => {
              this.$message({
                type: 'info',
                message: ''
              });
            });
          }else{
            this.NowChat = true
            this.replay = '' + a.user.name
            localStorage.setItem('table_id',this.personList[b].table_id)
            localStorage.setItem('comment_id',a.comment_id)
          }
        },
        toPerson(item){
          this.$router.push({
            path: '/userdata',
            query: {
              information: item
            }
          })
        },
        toPersonA(item,index){
          this.$router.push({
            path: '/userdata',
            query: {
              information: item.user.user_id
            }
          })
        },
        getWeekDay(str){
          var weekDay = ["", "", "", "", "", "", ""];
          var myDate = new Date(Date.parse(str));
          return weekDay[myDate.getDay()]
        },
        save(item,index){
          if(item.is_this_colle == 1){
            this.$http.post(this.api + '/home/Comment/collection',{
              token: localStorage.getItem('token'),
              flag: 1,
              table_id: this.routerParams,
              group_id: item.group_id,
              type: 2
            })
              .then(res=>{
                if(res.data.code == 1){
                  let a=[]
                  item.is_this_colle = 0
                  for(let i =0;i<this.wishList.length;i++){
                    if(this.wishList[i].is_this_colle == 0){
                      a.push(i)
                      if(a.length == this.wishList.length){
                        this.is_collection = 0
                      }
                    }
                  }
                }else if(res.data.code == 3 || res.data.code == 4){
                  this.save(item,index)
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }else{
            this.$http.post(this.api + '/home/Comment/collection',{
              token: localStorage.getItem('token'),
              flag: 1,
              table_id: this.routerParams,
              group_id: item.group_id,
              type: 1
            })
              .then(res=>{
                if(res.data.code == 1){
                  item.is_this_colle = 1
                  this.is_collection = 1
                }else if(res.data.code == 1){
                  this.$http.post(this.api + '/home/Index/token')
                    .then(res=>{
                      localStorage.setItem('token',res.data.data)
                      this.save(item,index)
                    })
                }else if(res.data.code == 0){
                  alert(res.data.msg)
                }
              })
          }
        },
        addwish(){
          if(this.wishIndex == 0){
            this.newWish = true
            this.wishIndex = -1
          }else{
            this.newWish = false
            this.wishIndex = 0
          }
        },
        getComment(val){
          this.$http.post(this.api + '/home/Comment/comment_list',{
            token: localStorage.getItem('token'),
            table_id:this.routerParams,
            flag: 1,
            order: 1,
            page: val
          })
            .then(res=>{
              if(res.data.code == 1){
                let data = res.data.data.data
                data.forEach((item,index)=>{
                  this.personList.push(item)
                })
                
                this.total =  res.data.data.total
              }else if(res.data.code == 3 || res.data.code == 4){
                this.getComment(val)
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        parise(a,b){
          let table_id = ''
          let flag = ''
          let type=''
          if(b == 1){
            table_id = a.comment_id
            flag = 3
          }else{
            table_id = a.msg_id
            flag = 4
          }
          if(a.is_praise){
            type = 2
          }else{
            type = 1
          }
          this.$http.post(this.api + '/home/Comment/praise',{
            token: localStorage.getItem('token'),
            flag: flag,
            table_id: table_id,
            type: type
          })
            .then(res=>{
              if(res.data.code == 1){
                a.is_praise = type == 2? 0 : 1
                a.praise_num = type == 2? a.praise_num - 1 : a.praise_num +1
                this.$message({
                  type:'success',
                  message: ''
                })
              }else if(res.data.code == 3 || res.data.code == 4){
                this.parise(a,b)
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        getInform(){
          this.$http.post(this.api + '/home/Activity/question',{
            token: localStorage.getItem('token'),
            flag: 6
          })
            .then(res=>{
              if(res.data.code == 1){
                this.informList = []
               for(let i = 0;i<res.data.data[0].option.length;i++){
                 let a = res.data.data[0].option[i].name.split('/')
                 this.informList.push({name: a[0],content:a[1],option_id:res.data.data[0].option[i].option_id})
               }
              }else if(res.data.code == 3 || res.data.code == 4){
                this.getInform()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        //
        getActls(){
          this.$http.post(this.api + '/ActlsTwo',{
            token: localStorage.getItem('token'),
            activity_id: this.routerParams,
            per_page:2,
            page:1
          })
            .then(res=>{
              if(res.data.code == 1){
                let data = res.data.data.data
                this.listA = data
              }else if(res.data.code == 3 || res.data.code == 4){
                this.getActls()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        reload(res){
          if(res){
            let data = res.data.data
            this.isOwer = res.data.data.user_id
            this.isvolunteer = res.data.data.isvolunteer
            if(data.audit_idcard){
              this.isChoose = true
            }else{
              this.isChoose = false
            }
          }
        },
        getOwers(){
          this.$http.post(this.api + '/home/User/get_user',{
            token: localStorage.getItem('token')
          })
            .then(res=>{
              if(res.data.code == 1){
                let data = res.data.data[0]
                this.isOwer = data.user_id
                this.isvolunteer = data.isvolunteer
                if(data.audit_idcard){
                  this.isChoose = true
                }else{
                  this.isChoose = false
                }
              }else if(res.data.code == 3 || res.data.code == 4){
                this.getOwers()
              }else if(res.data.code == 0){
                alert(res.data.msg)
              }
            })
        },
        toPublish(item){
          this.$router.push({
            path: '/publishPage',
            query: {
              information: item.activity_id
            }
          })
        }
      },
      // destroyed() {
      //   window.removeEventListener('scroll',this.ScrollContent)
      // },
    }



